<template>
    <div class="dress-classify-model">
        <div class="dress-classify-image">
            <div class="dress-classify-image-container">
                <img :src="imgSrc" class="dress-image">
            </div>
            <div class="dress-classify-button">
                <img src="../../assets/img/DressingRoom/Dress/last.png" @click="last">
                <span>{{name}}</span>
                <img src="../../assets/img/DressingRoom/Dress/next.png" @click="next">
            </div>
        </div>
        <GeminiScrollbar class="scroll">
            <div class="dress-classify-intro">{{content}}</div>
        </GeminiScrollbar>


    </div>
</template>

<script>
    export default {
        name: "ClassifyCom",
        data:function(){
            return{
                index:0,
                name:'',
                content:'\n' +
                    '\n' +
                    '\n' +
                    ''+
                    '\n' +
                    '\n' +
                    '\n' +
                    '\n' +
                    ''+'\n' +
                    '\n' +
                    '',
                imgSrc:require('../../assets/img/DressingRoom/Dress/mang.png'),
                contentMap:new Map([
                    ['0',
                        {
                            content:'\n' +
                                '\n' +
                                '\n' +
                                ''+
                                '\n' +
                                '\n' +
                                '\n' +
                                '\n' +
                                ''+'\n' +
                                '\n' +
                                '',
                            name:'',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/mang.png'),
                        }
                    ],
                    ['1',
                        {
                            content:'\n' +
                                '\n' +
                                '\n' +
                                '',
                            name:'',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/kao.png'),
                        }
                    ],
                    ['2',
                        {
                            content:'\n' +
                                '\n' +
                                '\n' +
                                '',
                            name:'',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/pi.png')
                        }
                    ],
                    ['3',
                        {
                            content:'\n' +
                                '\n' +
                                '*\n' +
                                '\n' +
                                '',
                            name:'',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/zhezi.png'),
                        }
                    ],
                    ['4',
                        {
                            content:'\n' +
                                '\n' +
                                '\n' +
                                '\n' +
                                ' ',
                            name:'',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/kaichang.png'),
                        }
                    ],
                    ['5',
                        {
                            content:'\n' +
                                '\n' +
                                '\n' +
                                '' +
                                '' +
                                '\n' +
                                '',
                            name:'',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/guanyi.png'),
                        }
                    ],
                    ['6',
                        {
                            content:'\n' +
                                '\n' +
                                '\n' +
                                '',
                            name:'',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/yi.png'),
                        }
                    ]
                ])
            }
        },
        methods:{
            next:function(){
                this.index++;
                if(this.index>6){
                    this.index=0;
                }
                this.content=this.contentMap.get(this.index.toString()).content;
                this.name=this.contentMap.get(this.index.toString()).name;
                this.imgSrc=this.contentMap.get(this.index.toString()).imgSrc;
            },
            last:function () {
                this.index--;
                if(this.index<0){
                    this.index=6;
                }
                this.content=this.contentMap.get(this.index.toString()).content;
                this.name=this.contentMap.get(this.index.toString()).name;
                this.imgSrc=this.contentMap.get(this.index.toString()).imgSrc;
            }
        }
    }
</script>

<style scoped>
    .dress-classify-model{
        display:flex;
    }
    .dress-classify-image{
        width:15rem;
        height:16rem;
        /*border:0.1rem solid #000000;*/
    }
    .dress-classify-image-container{
        width:15rem;
        height:13rem;
    }
    .dress-classify-intro{
        width:52rem;
        height:16rem;
        /*border:0.1rem solid #000000;*/
        margin-left:2rem;
        line-height: 2rem;
        /*overflow: scroll;*/
    }
    .dress-classify-button{
        width:8rem;
        display:flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 auto;
    }
    .dress-classify-button img{
        line-height: 1rem;
        vertical-align: middle;
        height:100%;
        flex-shrink: 0;
        cursor: pointer;
    }
    .dress-image{
        width:13rem;
        margin: 0 auto;
        display: block;
    }
    .scroll{
        height: 16rem;
    }


</style>