<template>
    <div class='theater'>
        <svg data-step="1" data-intro="" class="theater-svg" :width="svgWidth" :height="svgHeight" viewBox="0 0 1200 660">

        </svg>
        <div class="display-select" data-step="2" data-intro="">
            <div class="header">
                <span class="camera-logo"></span>
                <span></span>
            </div>
            <div class="content">
                <span class="tag"
                      :class="displaySelected === 0? 'tag-selceted':''"
                      @click="changeTag(0, 'type')"
                ></span>
                <span class="tag"
                      data-index="1"
                      :class="displaySelected === 1? 'tag-selceted':''"
                      @click="changeTag(1, 'sound')"
                ></span>
                <span class="tag"
                      data-index="2"
                      :class="displaySelected === 2? 'tag-selceted':''"
                      @click="changeTag(2, 'color')"
                ></span>
                <div class="flag-point-box">
                    <div v-for="(data, index) in flagPointsArray[displaySelected]">
                        <span :style="'background-color:' + data[0]" class="flag-point"></span>
                        <span>{{data[1]}}</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="theater-intro">
            
            11271127
        </div>

        <theater-tag-com
                v-show="showTag"
                :year="tagYear"
                :theater="tagTheater"
                :top="tagTop"
                :left="tagLeft"></theater-tag-com>
    </div>
</template>

<script>
    import * as d3 from 'd3';
    import theaterJsonData from '../../assets/JSON/theaterData';
    import {getNumTranFun, guideCheck} from '../../assets/js/jsFuncModule';
    import TheaterTagCom from '../../components/TheaterTagCom';
    export default {
        name: 'Theater',
        components: {TheaterTagCom},
        data: function () {
            /**
             type
             0
             1
             2
             3
             4
             5

             sound
             0
             1
             2

             color
             0
             1
             2
             */
            return {
                displaySelected: 0, // 012
                flagPointsArray: [
                    [
                        ['#6f589c', ''],
                        ['#f15a23', ''],
                        ['#80c269', ''],
                        ['#f2be46', ''],
                        ['#b63127', ''],
                        ['#9ac4e9', '']
                    ],//
                    [
                        ['#b63127', ''],
                        ['#f2be46', ''],
                        ['#3c5282', '']
                    ],//
                    [
                        ['#b63127', ''],
                        ['#f2be46', ''],
                        ['#3c5282', '']
                    ],//
                ],//
                theaterData: null, //Map{  key:year, value:array[{},{},{}]  }  x
                svgWidth: null,
                svgHeight: null,
                showTag:false, //tag
                tagYear:"",//tag
                tagTheater:"",//tag
                tagLeft:0,//tagleft
                tagTop:0//tagtop
            };
        },
        methods: {
            theaterDataTran: function (jsonData) {
                const map = new Map();
                for (let item of jsonData) {
                    map.has(item.year) ? null : map.set(item.year, new Array());
                    const yearArray = map.get(item.year);
                    yearArray.push({
                        x: 0,
                        movieName: item.movieName,
                        type: item.type,
                        sound: item.sound,
                        color: item.color,
                        year:item.year
                    });
                }
                return map;
            },
            changeTag: function (i, keyName) {
                this.displaySelected = i;
                this.sortMapData(keyName);
                this.pointsAnimation(i, keyName);
            },
            init: function () {
                const
                    mapData = this.theaterData,
                    svg = d3.select('.theater-svg'),
                    svgPadding = {
                        top: 30,
                    },
                    fontSize = 13;

                let currentYear = parseInt(mapData.keys().next().value),//
                    yRecord = svgPadding.top, //barGsvgyybarG
                    mapIndex = 0;


                for (let [key, array] of mapData) {
                    // console.log(array);
                    const
                        year = parseInt(key),
                        barG = svg.append('g'),//g
                        text = barG.append('text').text(year).style('font-size', fontSize),//
                        pointG = barG.append('g').attr('class', 'points-g').attr('transform', 'translate(50, -5)'),//g
                        points = pointG.selectAll('circle').data(array);

                    const
                        logScale = d3.scaleLog().domain([1, 50]).range([0, 57]);

                    barG.attr('transform', () => {
                        yRecord += logScale(year - currentYear + 1);
                        return `translate(-88, ${yRecord})`;
                    });
                    text.text(() => {
                        if (year - currentYear >= 2 || year - currentYear === 0) {//
                            return year + ' '
                        } else {
                            return '';
                        }
                    });
                    const that = this;//functionthis
                    points.join('circle')
                        .attr('r', 4)
                        .style('cursor','pointer')
                        .on('mouseover',function(d, i){
                        d3.select(this).style('stroke', 'black').style('stroke-width',3);

                        that.showTag = true;
                        that.tagLeft = d3.event.clientX + 5;
                        that.tagTop = d3.event.clientY + 5;
                        that.tagYear = d.year;
                        that.tagTheater = d.movieName;



                    }).on('mouseout',function(d,i){
                        that.showTag = false;
                        d3.select(this).style('stroke', null).style('stroke-width',null);
                    });
                    currentYear = year;
                    mapIndex++;
                }


            },
            sortByKeyName: function (array, keyName) {//'type''sound''color'
                array.sort(
                    function (a, b) {
                        return a[keyName] - b[keyName];
                    }
                );
            },
            sortMapData: function (keyName) {
                const mapData = this.theaterData;
                for (let [key, array] of mapData) {
                    //
                    this.sortByKeyName(array, keyName);
                    //x
                    const length = array.length;
                    for(let i = 0; i < length; i++){
                        array[i].x = i * 12;
                    }
                }
            },
            /**
               .points-gdataupdate
               flagPointIndexdataflagPointsArray
             */
            pointsAnimation: function (flagPointIndex, keyName) {
                const mapData = this.theaterData,
                      pointGList = d3.selectAll('.points-g'),
                      t = d3.transition().duration(500).ease(d3.easePoly),
                      colorArray = this.flagPointsArray[flagPointIndex];
                pointGList.each(function (d, i) {
                    const pointsG = d3.select(this);
                    const points = pointsG.selectAll('circle');
                    points
                        .transition(t)
                        .style('fill',(d)=>{
                            const a = colorArray[d[keyName]];
                            return a[0];
                        })
                        .transition(t)
                        .attr('cx',(d)=>{
                        return d.x;})
                });


            }
        },
        watch: {
            displaySelected: function (newSelected, oldSelected) {

            }
        },
        created: function () {
            this.theaterData = this.theaterDataTran(theaterJsonData.data);
            const tran = getNumTranFun();
            this.svgWidth = tran(1204);
            this.svgHeight = tran(570);
            // window.addEventListener('resize', this.init);
        },
        mounted: function () {
            this.init();
            this.sortMapData('type');
            this.pointsAnimation(0, 'type');

            guideCheck(500,'Theater');
            // setTimeout(() => {
            //     const introJS = require('intro.js');
            //     const introjs = introJS()
            //         .setOption("skipLabel", "")
            //         .setOption('nextLabel', '')
            //         .setOption('prevLabel', '')
            //         .setOption('doneLabel','');
            //     //transition
            //     introjs.start();
            // }, 500);
        }
    };
</script>

<style scoped>
    .theater {
        position: relative;
    }
    .theater-intro{
        font-size: 0.8rem;
        position: absolute;
        right: 6.5rem;
        top: 1rem;
        width: 16rem;
        line-height: 1.2rem;
    }
    .theater-svg {
        display: block;
        margin: 0 auto;
    }

    .display-select {
        position: absolute;
        right: 6.5rem;
        bottom: 1.5rem;
        color: #3c5282;
    }

    .display-select .header {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        margin-bottom: 0.3rem;
    }

    .display-select span {
        display: inline-block;
        cursor: pointer;
    }

    .camera-logo {
        width: 2.375rem;
        height: 2.375rem;
        background-image: url("../../assets/svg/Theater/camera.svg");
        background-repeat: no-repeat;
        background-size: contain;
        margin-right: 1rem;
    }

    .display-select .content {
        border: 2px dashed #3c5282;
        padding: 1rem 0.5rem 0.5rem 0.5rem;
        border-radius: 3%;
    }

    .display-select .content .tag {
        display: inline-block;
        vertical-align: middle;
        margin: 0 1rem;
        color: #aaaaaa;
        border-bottom: 2px solid transparent;
        padding-bottom: 2px;
    }

    .display-select .content .tag:hover {
        color: #3c5282;
        border-bottom: 2px solid #3c5282;
    }

    .display-select .content .tag-selceted {
        color: #3c5282;
        border-bottom: 2px solid #3c5282;
    }

    .flag-point-box {
        vertical-align: middle;
        margin: 0.5rem 0 0 0.5rem;
        height: 7rem;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }

    .flag-point-box .flag-point {
        display: inline-block;
        width: 0.65rem;
        height: 0.65rem;
        border-radius: 50%;
    }

    .flag-point-box div {
        margin: 0.5rem 0 0.5rem 0.5rem;
    }

    .flag-point-box div span {
        vertical-align: middle;
    }
</style>
