<template>
    <div class="face-model">
        <div class="face-content">
            <div class="cantonese-opera-face">
                <div></div>
                <div></div>
                <div></div>
                <div></div>
            </div>
            <div class="face-introduction">
                <p>
                    </p>
                <p></p>
                <p>1. </p>
                <p>2. </p>
                <p>3. </p>
