                <p></p>
            </div>
            <div class="face-image" data-id="" @click="selected">
                <img :src="require('../../../assets/img/DressingRoom/Face/zhaokuangyi.png')">
                <p></p>
                <p></p>
            </div>
            <div class="face-image" data-id="" @click="selected">
                <img :src="require('../../../assets/img/DressingRoom/Face/caocao.png')">
                <p></p>
                <p></p>
            </div>
            <div class="face-image" data-id="" @click="selected">
                <img :src="require('../../../assets/img/DressingRoom/Face/wangyanzhangv.png')">
                <p></p>
                <p></p>
            </div>
        </div>
        <div class="face-image-container">
            <div class="face-image" data-id="" @click="selected">
                <img :src="require('../../../assets/img/DressingRoom/Face/zhangfei.png')">
                <p></p>
                <p></p>
            </div>
            <div class="face-image" data-id="" @click="selected">
                <img :src="require('../../../assets/img/DressingRoom/Face/mawu.png')">
                <p></p>
                <p></p>
            </div>
            <div class="face-image" data-id="" @click="selected">
                <img :src="require('../../../assets/img/DressingRoom/Face/zhongwuyan.png')">
                <p></p>
                <p></p>
            </div>
            <div class="face-image" data-id="" @click="selected">
                <img :src="require('../../../assets/img/DressingRoom/Face/likui.png')">
                <p></p>
                <p></p>
            </div>
        </div>
        <face-intro-com v-on:toggle-intro="toggleIntro" v-show="showIntro" :name="name"  :img-src="imgSrc" :content="content"></face-intro-com>
    </div>
</template>

<script>
    import FaceIntroCom from '../../../components/FaceIntroCom'
    export default {
        components:{
            FaceIntroCom:FaceIntroCom
        },
        name: "face",
        data:function () {
            return{
                name:'',
                imgSrc:require('../../../assets/img/DressingRoom/Face/wendashi.png'),
                content:'',
                contentMap:new Map([
                    ['',
                        {
                            imgSrc:require('../../../assets/img/DressingRoom/Face/wendashi.png'),
                            content:''
                        }
                    ],
                    ['',
                        {
                            imgSrc:require('../../../assets/img/DressingRoom/Face/zhaokuangyi.png'),
                            content:''
                        }
                     ],
                    ['',
                        {
                            imgSrc:require('../../../assets/img/DressingRoom/Face/caocao.png'),
                            content:''
                        }
                     ],
                    ['',
                        {
                            imgSrc:require('../../../assets/img/DressingRoom/Face/zhongwuyan.png'),
                            content:'' +
                                ''
                        }
                    ],
                    ['',
                        {
                            imgSrc:require('../../../assets/img/DressingRoom/Face/wangyanzhangv.png'),
                            content:'' +
                                ''
                        }
                    ],
                    ['',
                        {
                            imgSrc:require('../../../assets/img/DressingRoom/Face/likui.png'),
                            content:''
                        }
                    ],
                    ['',
                        {
                            imgSrc:require('../../../assets/img/DressingRoom/Face/zhangfei.png'),
                            content:''
                        }
                    ],
                    ['',
                        {
                            imgSrc:require('../../../assets/img/DressingRoom/Face/mawu.png'),
                            content:''
                        }
                    ]
                ]),
                showIntro:false
            }
        },
        methods:{
            toggleIntro:function(){
                this.showIntro = !this.showIntro;
            },
            selected:function(e){
                this.name=e.currentTarget.getAttribute('data-id');
                this.imgSrc=this.contentMap.get(e.currentTarget.getAttribute('data-id')).imgSrc;
                this.content=this.contentMap.get(e.currentTarget.getAttribute('data-id')).content;
                this.showIntro = true;
            }
        }
    }
</script>

<style scoped>
    .face-model{
        margin-top: 1rem;
    }
    .face-image-container div{
        cursor: pointer;
    }
    .cantonese-opera-face{
        display: inline-block;
        vertical-align: top;

        /*float: left;*/
    }
    .cantonese-opera-face div{
        width:3rem;
        height:3rem;
        text-align: center;
        margin:0 0.5rem 0.5rem 0.5rem;
