<template>
    <div class="popular-science">
     <div class="popular-science-type-container">
        <div class="popular-science-type" @click="rotateCard" data-id="0" :class="[index === '0'? 'rotateCard':'unRotate']"></div>
        <div class="popular-science-type" @click="rotateCard" data-id="1" :class="[index === '1'? 'rotateCard':'unRotate']"></div>
        <div class="popular-science-type" @click="rotateCard" data-id="2" :class="[index === '2'? 'rotateCard':'unRotate']"></div>
        <div class="popular-science-type" @click="rotateCard" data-id="3" :class="[index === '3'? 'rotateCard':'unRotate']"></div>
        <div class="popular-science-type" @click="rotateCard" data-id="4" :class="[index === '4'? 'rotateCard':'unRotate']"></div>
        <div class="popular-science-type" @click="rotateCard" data-id="5" :class="[index === '5'? 'rotateCard':'unRotate']"></div>
        <div class="popular-science-type" @click="rotateCard" data-id="6" :class="[index === '6'? 'rotateCard':'unRotate']"></div>
        <div class="popular-science-type" @click="rotateCard" data-id="7" :class="[index === '7'? 'rotateCard':'unRotate']"></div>
        <div class="popular-science-type" @click="rotateCard" data-id="8" :class="[index === '8'? 'rotateCard':'unRotate']"></div>
     </div>
        <div class="popular-science-introduction">{{type}}{{initContent}}</div>
    </div>
</template>
<script>
    export default {
        name: "popular-science",
        data:function () {
            return{
                index:' ',
                type:'',
                initContent:'?',
                contentMap:new Map([
                    ['','6' +
                    ''],
                    ['',''],
                    ['','' +
                    ''],
                    ['',''],
                    ['',''],
                    ['',''],
                    ['',''],
                    ['',''],
                    ['','']
                ])
            }
        },
        methods:{
            rotateCard:function(e){
                this.index=e.target.getAttribute("data-id");
                this.initContent=this.contentMap.get(e.target.innerHTML);
                this.type=e.target.innerHTML+':';
            }
        }

    }
</script>

<style scoped>
    .popular-science-type-container{
        width:60rem;
        height:9rem;
        display:flex;
        justify-content: space-between;
        margin:6rem auto;
    }
    .unRotate{
        width:5rem;
        height:8rem;
        text-align: center;
        line-height: 5rem;
        writing-mode: vertical-lr;
        font-size: 2rem;
        background-image:url("../../../src/assets/img/PopularScience/card.png");
        background-size: 100% 100%;
        background-repeat: no-repeat;
        cursor: pointer;
    }
    @keyframes rotateCard {
        0% {transform: rotateY(0deg);}
        100% {transform: rotateY(360deg);}
    }
    .rotateCard{
        animation: rotateCard 2s;
        perspective: 5rem;
        width:5rem;
        height:8rem;
        text-align: center;
        line-height: 5rem;
        writing-mode: vertical-lr;
        font-size: 2rem;
        background-image: url("../../../src/assets/img/PopularScience/card-click.png");
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }
    .popular-science-introduction{
        width:60rem;
        margin:0 auto;
        font-size:1.5rem;
        line-height: 2rem;
        font-weight: normal;
    }
</style>